import {CustomTitle} from '../components/custom';
import {Container, Select, HStack, Text, Grid, Button, } from "@chakra-ui/react";
import{ChevronRightIcon} from '@chakra-ui/icons'; 
import {useState} from 'react';

interface nonTransitiveDice{
  title: string;
}

const dice = {
  red : [4, 4, 4, 4, 4, 9],
  blue : [2, 2, 2, 7, 7, 7],
  olive : [0, 5, 5, 5, 5, 5],
  yellow : [3, 3, 3, 3, 8, 8],
  magenta : [1, 1, 6, 6, 6, 6],
}
const numberDice = {
  'one die': 1,
  'two dice': 2,
}

const buildField = (size:number) => {
  let x = new Array(6**size);
  for (let i = 0; i < x.length; i++) {
      x[i]=[];
    for (let j = 0; j < x.length; j++) {
      x[i].push(0);    
  }}
  return x
}


const NonTransitiveDice: React.FC<nonTransitiveDice> =({title}) => {
  const [state, setstate] = useState<(keyof typeof dice)[]>(['red', 'red'])
  const [field, setField] = useState<(number)[][]>(buildField(numberDice['one die']));
  const [number, setNumber] = useState(numberDice['one die']);
  const [hidden, setHidden] = useState(true);
  

  const subject = ['Física', 'Mecânica', 'Dinâmica', 'Força', 'Força de Atrito'];

  const NotHidden = () => setHidden(false);

  return (
    <>
    
      <Container centerContent={true} >
      <CustomTitle title={title}/>
        <Grid   templateRows="repeat(2, 1fr)" gap={1} mb='20px'>
          {state.map((colour, i)=>(!(i===1 && state[1]===state[0]) && !hidden ?
          <Grid key={i} templateColumns="repeat(2, 1fr)" gap={6}>
            <Text visibility={hidden?'hidden':'visible'}  style={{color:'white', backgroundColor:'black', opacity:'70%'}}>
              {`${colour[0].toUpperCase() +colour.slice(1)} die: `}
            </Text>
            <Text visibility={hidden?'hidden':'visible'} style={{color:'white', backgroundColor:'black', opacity:'70%'}}>
              {`[${dice[colour]}]`}
            </Text>
          </Grid>:<></>))}
        </Grid>
      <HStack spacing={4} mb='20px'>
      <Select style={{fontSize:'8px'}} placeholder=""  onChange={(e)=>{ NotHidden();
                                              const num = parseInt(e.target.value);
                                              setNumber(num);
                                              BuildField(dice[state[0]  as keyof typeof dice], dice[state[1] as keyof typeof dice], setField, num);                                              
                                              }}>
        {Object.keys(numberDice).map((key)=><option key={key} style={{color:'white', backgroundColor:'black'}} value={''+numberDice[key  as keyof typeof numberDice]}>{key.toUpperCase()}</option>)}
      </Select>
      <Select placeholder="" style={{color:(state[0] === 'yellow'?'black':'white'), backgroundColor:state[0]}} onChange={(e)=>{ NotHidden();
                                                                                                                                setstate(prevState => [e.target.value as keyof typeof dice, prevState[1]]);
                                                                                                                                BuildField(dice[e.target.value as keyof typeof dice], dice[state[1] as keyof typeof dice], setField, number);
                                                                                                                                
                                                                                                                                }}>
        {Object.keys(dice).map((key)=><option key={key} style={{color:'white', backgroundColor:'black'}} value={key}>{key.toUpperCase()}</option>)}
      </Select>
      <Select placeholder="" style={{color:(state[1] === 'yellow'?'black':'white'), backgroundColor:state[1]}} onChange={(e)=>{ NotHidden();
                                                                                                                                setstate(prevState => [prevState[0],e.target.value as keyof typeof dice]);
                                                                                                                                BuildField(dice[state[0] as keyof typeof dice], dice[e.target.value as keyof typeof dice], setField, number);                                                                                                                                
                                                                                                                                
                                                                                                                                }}>
        {Object.keys(dice).map((key)=><option key={key} style={{color:'white', backgroundColor:'black'}} value={key}>{key.toUpperCase()}</option>)}
      </Select>
      </HStack>
      <Text style={{color:'white', opacity:'60%', textAlign:'center'}} mt='5px' mb='15px'>
      {`The probability of ${number===numberDice['one die']? 'one':'two'} ${state[0]}
       ${number===numberDice['one die']? 'die':'dice'} winning against 
       ${number===numberDice['one die']? 'one':'two'} ${state[1]} 
       ${number===numberDice['one die']? 'die':'dice'} is ${probability(dice[state[0] as keyof typeof dice], dice[state[1] as keyof typeof dice], number)}`}
      </Text >
      
      <Grid templateColumns={`repeat(${6**number}, 1fr)`} templateRows={`repeat(${6**number}, 1fr)`} gap={number===numberDice['one die']?'5px':'1px'} >                                                                                                                 
      {field.map((line) => (
            <>
            {line.map((element)=> (
              <Container m='0' p='0' visibility={hidden || (state[0]===state[1])?'hidden':'visible'} style={{height: number===numberDice['one die']?'30px':'5px', width: number===numberDice['one die']?'30px':'5px',backgroundColor: state[0]===state[1]?'white':element===1?state[0]:(element===-1?state[1]:'white'), opacity:'100%'}}/>
                
            ))}
            </>
      ))}
      </Grid>  
    

      <Button width='100%' mt='20px' colorScheme='blue' onClick={()=>alert(JSON.stringify(subject))}>
        {subject.map((sub, index) => (
          <>
          <Text isTruncated={!(index === 0 || index=== subject.length-1)}>
          {sub}
          </Text>
          {!(index=== subject.length-1) &&  
            <ChevronRightIcon/>}
          </>
        ))}
      </Button>
      
      </Container>

    </>
  )
}

export default NonTransitiveDice

const BuildField = (dieA:Array<number>, dieB:Array<number>, setField:React.Dispatch<React.SetStateAction<number[][]>>, size:number) => {
    
  let x = buildField(size);

  
  let dieAnew:number[][] = [];
  let dieBnew:number[][] = [];

  if (size === numberDice['one die']){      
    for(const faceA of dieA) 
      dieAnew.push([faceA]);
    for(const faceB of dieB) 
      dieBnew.push([faceB]);
  }

  if (size === numberDice['two dice']){
    for(const faceA1 of dieA) 
      for(const faceA2 of dieA)
        dieAnew.push([faceA1, faceA2]);
    for(const faceB1 of dieB) 
      for(const faceB2 of dieB)
          dieBnew.push([faceB1, faceB2]);
  }



  
  for(let a in dieAnew) {
    for(let b in dieBnew){
      if(dieAnew[a].reduce(function(acc, val) { return acc + val; }, 0) > dieBnew[b].reduce(function(acc, val) { return acc + val; }, 0))
        x[a][b] = 1;
        if(dieAnew[a].reduce(function(acc, val) { return acc + val; }, 0) < dieBnew[b].reduce(function(acc, val) { return acc + val; }, 0))
        x[a][b] = -1;
    }
  }
  setField(x);
}


const probability = (dieA:Array<number>, dieB:Array<number>, size:number) => {
    

    let aWins = 0;
    let bWins = 0;
    let dieAnew:number[][] = [];
    let dieBnew:number[][] = [];

    if (size === numberDice['one die']){      
      for(const faceA of dieA) 
        dieAnew.push([faceA]);
      for(const faceB of dieB) 
        dieBnew.push([faceB]);
    }

    if (size === numberDice['two dice']){
      for(const faceA1 of dieA) 
        for(const faceA2 of dieA)
          dieAnew.push([faceA1, faceA2]);
      for(const faceB1 of dieB) 
        for(const faceB2 of dieB)
            dieBnew.push([faceB1, faceB2]);
    }


    for(const faceA of dieAnew) {
      for(const faceB of dieBnew){
        if(faceA.reduce(function(acc, val) { return acc + val; }, 0) > faceB.reduce(function(acc, val) { return acc + val; }, 0))//(Math.max(...faceA) > Math.max(...faceB))//.reduce(function(acc, val) { return acc + val; }, 0)
          aWins++;
        if(faceA.reduce(function(acc, val) { return acc + val; }, 0) < faceB.reduce(function(acc, val) { return acc + val; }, 0))
          bWins++;
      }
    }
    return (
      `${aWins+bWins === 0? 0: Math.round(10000*aWins/(aWins+bWins))/100}%`
    )
}
