import {Page} from  "../dataStructure/page";
import { Button, HStack, VStack, Container, Box, Collapse } from "@chakra-ui/react";
import {DeleteIcon} from "@chakra-ui/icons";
import firebase from '../firebase';
import {useEffect, useState} from 'react';
import newUser from '../dataStructure/user';
import {saveStateAndFirestore} from '../helpers/save';
import {Formik, 
  Form}  from 'formik';
  import * as Yup from 'yup';  
import {CustomTextInput, CustomTitle} from '../components/custom';



interface dashboard {
    title:string,
    changePage: any,
}
const Dashboard:React.FC<dashboard> = ({title, changePage}) => {
    
  //localStorage.clear();
    const email = localStorage.getItem('email');
    const ref = firebase.firestore().collection('users').doc('users_document').collection(email as string).doc('data');
    //console.log(ref);
    
  
    
    const [dataFireStore, setDataFireStore ] = useState<typeof newUser>(newUser);    
    const [dataFireStoreBackup, SetDataFireStoreBackup ] = useState<typeof newUser | null>(null);
    const [dataFireStoreBackupArray, SetDataFireStoreBackupArray ] = useState<(typeof newUser)[]>([]);

    const effect = () => {
      ref.onSnapshot((querySnapshot) => {
        setDataFireStore(querySnapshot.data()? querySnapshot.data() as typeof newUser: newUser);
        })
    }
  


    

     
    const [show, setShow] = useState(false);
    const handleToggle = () => setShow(!show);

  useEffect(() => {

      effect(); 
                                          
    }, []);


    

    return (
        <>    
            <CustomTitle title={title}/>            
            
            <Box id='box' mb={'10px'}>            

            <Formik 
              initialValues={{
              newList:'',
              }}
              validationSchema={Yup.object({
              newList: Yup.string()
              .required('You must give it a name.')
              .notOneOf(Object.keys(dataFireStore.lists), 'This list already exists' ),

              })}
              onSubmit={(values, {setSubmitting, resetForm}) => {
                  setTimeout(()=> {
                      const data = dataFireStore;
                      const new_list =  values.newList.charAt(0).toUpperCase() + values.newList.slice(1);
                      data.list = new_list;
                      
                      data.lists[new_list] =  [];
                      saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});

                      setTimeout(() => changePage(Page.List), 500);
                      resetForm();
                      setSubmitting(false);
                  }, 500);
              }}
              >

                  {props => (

                      <Form>
                          <Container centerContent={true}>                             
                          <VStack spacing={3}>  
                          <Collapse  in={show}>
                          <HStack spacing={3}>    
                          <VStack spacing={1}>              
                          <CustomTextInput  label='' name='newList' type='text' placeholder='new list' handleBlur={props.handleBlur}/>              
                          </VStack>
                          <VStack spacing={1}> 
                          <Button    type='submit' colorScheme='blue' className='button' > {props.isSubmitting? 'Loading...':'add'}</Button>

                          <Box mb={props.errors.newList?'20px':'0px'} height={props.errors.newList?'0px':'2px'}  >
                          </Box>
                          </VStack>
                          </HStack>                          
                          </Collapse> 
                          
                          <HStack spacing={3}>
                          <Button   colorScheme="blue" onClick={() => {
                              props.resetForm();
                              handleToggle();
                              setTimeout(() =>{
                                const field = (document.getElementsByName("newList")[0] as HTMLElement);
                                field.focus();                   
                              }, 100); 
                              }}>
                              {show? 'Cancel':'New List'}
                          </Button>   
                          {dataFireStoreBackupArray.length > 0 &&<Button colorScheme='blue' onClick={() =>{
                            const data = dataFireStoreBackupArray[dataFireStoreBackupArray.length-1];
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                            SetDataFireStoreBackupArray(dataFireStoreBackupArray.slice(0,dataFireStoreBackupArray.length-1));
                            console.log(data);
                          }} >
                            Undo
                          </Button>}
                          </HStack>

                          </VStack>
                          </Container>
                      </Form>
                  )}


              </Formik>            

            </Box>     
             
            <Box id='box' m={'20px'} style={{width: '80%', backgroundColor: '#dcecf7', borderRadius:'20px' }}
            onChange={() => {
                
            }}>
                {Object.keys(dataFireStore.lists).sort().map((item, i) => 
                    <div  key={i} style={{color: "#3182ce", fontSize:"20px", margin: '10px'}} >
                        
                        <HStack spacing={5}>
                        <DeleteIcon color="gray.300" onClick={() => {                          
                            const dataBackup = JSON.parse(JSON.stringify(dataFireStore));
                            //SetDataFireStoreBackup(dataBackup);                            
                            SetDataFireStoreBackupArray(last => last !== []?[...last, (dataBackup as (typeof newUser))]:[(dataBackup as (typeof newUser))]);
                            console.log([dataFireStoreBackupArray, (dataBackup as (typeof newUser))]);

                            const data = dataFireStore;
                            delete data.lists[item];
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                            
                             

                             
                        }}/>
                        
                        <Box onClick={() => {
                            

                            const data = dataFireStore;
                            data.list = item;
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                                                    
                            setTimeout(() => changePage(Page.List), 50);
                        }}
                        >{`${item}`}</Box>
                            
                        </HStack>
                        
                        
                    </div>
                )}
            </Box>
            
            {false && <Button colorScheme='blue' onClick={() => changePage(Page.Dice)} >
              Non-Transitive Dice
            </Button>}






        </>
    );
}

export default Dashboard;