import { Button, HStack, VStack, Container, Box, Checkbox, Fade, Collapse } from "@chakra-ui/react";
import {Page} from  "../dataStructure/page";
import NewTask from "../components/newTask";  
import {DeleteIcon} from "@chakra-ui/icons";
import firebase from '../firebase';
import {useEffect, useState} from 'react';
import newUser from '../dataStructure/user';
import {saveStateAndFirestore} from '../helpers/save';
import { DragDropContext, Droppable, Draggable } from 'react-beautiful-dnd';
import {Formik, 
  Form}  from 'formik';
  import * as Yup from 'yup';  
import {CustomTextInput, CustomTitle} from '../components/custom';

interface list {
    title:string,
    changePage: any,
}
const List:React.FC<list> = ({title, changePage}) => {   
    const email = localStorage.getItem('email');
    const ref = firebase.firestore().collection('users').doc('users_document').collection(email as string).doc('data');
        
    const [dataFireStore, setDataFireStore ] = useState<typeof newUser>(newUser);
    const [dataFireStoreBackup, SetDataFireStoreBackup ] = useState<typeof newUser>(newUser);

    const effect = () => {
      ref.onSnapshot((querySnapshot) => {
        setDataFireStore(querySnapshot.data()? querySnapshot.data() as typeof newUser: newUser);
        
        });
        ref.get().then((doc) => {
          if (doc.exists) {
            SetDataFireStoreBackup(doc.data() as typeof newUser);
          } 
        });
    }


    const [option, setOption] = useState('0');
    const [buttonVisibility, setButtonVisibility] = useState(true);
    const [show, setShow] = useState(false);
    const handleToggle = () => setShow(!show);

    const MAX_DIST = 35000;
    const mouse_Movement = (e:MouseEvent)=> {
      const save = document.getElementById('newItem');
      if(save && window.innerWidth > 500){
        const [saveX,saveY]=[save.getBoundingClientRect().x,save.getBoundingClientRect().y];
        const d =(e.clientX-saveX-100)*(e.clientX-saveX-100)  + (e.clientY-saveY-10)*(e.clientY-saveY-10);
        setButtonVisibility( d < MAX_DIST);
      }
      else  
        setButtonVisibility(true);
      
    }

    useEffect(() => {
      effect();     
      window.addEventListener('mousemove', mouse_Movement);     
      return () => {
        window.removeEventListener('mousemove', mouse_Movement);
      };                         
    }, []);



    function handleOnDragEnd(result:any) {
      if (!result.destination) return;      
      const data = dataFireStore;
      const [reorderedItem] = data.lists[data.list].splice(result.source.index, 1);
      data.lists[data.list].splice(result.destination.index, 0, reorderedItem);

      saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
    }

    return (
        <>    
      


            <Formik 
              initialValues={{
              newItem:'',
              }}
              validationSchema={Yup.object({
                newItem: Yup.string()
              .required('You must give it a name.')
              .notOneOf(Object.keys(dataFireStore.lists), 'This list already exists' ),

              })}
              onSubmit={(values, {setSubmitting, resetForm}) => {
                  setTimeout(()=> {
                      
                      if(option === '1'){
                          const data = dataFireStore;
                          data.lists[data.list] =[...data.lists[data.list].concat([`${values.newItem}!***!false!***!`])];
                          saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                      
                      }

                      if(option === '2'){
                        const data = dataFireStore;
                        
                        data.list = values.newItem;
                        data.lists[values.newItem] = data.lists[dataFireStoreBackup.list];
                        delete data.lists[dataFireStoreBackup.list];

                        saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                        
                        const dataBackup = dataFireStoreBackup;
                        dataBackup.list = values.newItem;
                        SetDataFireStoreBackup(dataBackup);

                                    
                        
                      }

                      setOption('0');

                      resetForm();
                      setSubmitting(false);                      
                      handleToggle();
                  }, 200);
              }}
              >

                  {props => (

                      <Form>
                     
                        <Collapse   in={!show} >
                        <Box  ml={'50%'} mt='10px' > 
                                     
                        <HStack spacing={3}>      
                        <Button  colorScheme="blue" size="xs" onClick={() => {                           
                            setOption('2');
                            props.resetForm();
                            handleToggle();
                            setTimeout(() =>{
                              const field = (document.getElementsByName("newItem")[0] as HTMLElement);
                              field.focus();                   
                            }, 100); 
                            }}>
                            Rename
                        </Button>

                        <Button  colorScheme="blue" size="xs" onClick={() => {
                                                                    const data = dataFireStoreBackup;
                                                                    data.list = '';                                                     
                                                                  saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                                    
                                                                  changePage(Page.DashBoard);}}>
                            Cancel
                        </Button> 
                        
                        
                        </HStack>
                        
                        </Box>
                        
                        </Collapse>



                          <Container centerContent={true}>    
                          
                          <CustomTitle title={dataFireStore.list}/>                         
                          <VStack spacing={3}>  
                          <Collapse  in={show}>
                          <HStack spacing={5}>    
                          <VStack spacing={1}>              
                          <CustomTextInput label='' name='newItem' type='text' placeholder='new item' handleBlur={props.handleBlur}/>              
                          </VStack>
                          <VStack spacing={1}> 
                          <Button type='submit' colorScheme='blue' className='button' > {props.isSubmitting? 'Loading...':option === '1'?'add':'rename'}</Button>
                          
                          <Box mb={props.errors.newItem?'20px':'0px'} height={props.errors.newItem?'0px':'2px'}  >
                          </Box>
                          </VStack>
                          </HStack>                          
                          </Collapse> 
                          <HStack spacing={8}> 
                          <Button colorScheme="blue" onClick={() => {
                            
                              setOption('1');
                              props.resetForm();
                              handleToggle();
                              setTimeout(() =>{
                                const field = (document.getElementsByName("newItem")[0] as HTMLElement);
                                field.focus();                   
                              }, 100); 
                              }}>
                              {show? 'Cancel':'New Item'}
                          </Button>  
                          <Fade in={buttonVisibility} id='newItem'>            
                          <Button  id='save' colorScheme="blue"  onClick={() => {
                                                                          
                                                                          changePage(Page.DashBoard);
                                                                          
                                                                          }}>
                              Save
                          </Button>              
                          </Fade>                                         
                          </HStack>
                          </VStack>
                          </Container>
                      </Form>
                  )}


              </Formik>            

            <Box id='box' m={'20px'}  p={'30px'} style={{width: '80%', backgroundColor: '#dcecf7', borderRadius:'20px' }}>
            <DragDropContext onDragEnd={handleOnDragEnd}>
            <Droppable droppableId="characters">
              {(provided) => (
                <Box  {...provided.droppableProps} ref={provided.innerRef}>
                  {dataFireStore.lists[dataFireStore.list] !== undefined? (dataFireStore.lists[dataFireStore.list] as string[]).map((item, i) => {
                    
                    return (
                      <Draggable key={i} draggableId={'' + i} index={i}>
                      {(provided) => (
                        <Box style={{fontSize:"20px", margin: '10px'}} ref={provided.innerRef} {...provided.draggableProps} {...provided.dragHandleProps}>
                        


                        <HStack spacing={5} >
                        <DeleteIcon color="gray.400" onClick={() => {
                            const aux1 = [...(dataFireStore.lists[dataFireStore.list]).slice(0,i)];
                            const aux2 = [...(dataFireStore.lists[dataFireStore.list]).slice(i+1,(dataFireStore.lists[dataFireStore.list]).length)];

                            const data = dataFireStore;
                            data.lists[data.list] = aux1.concat(aux2);
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                              
                        }}/>
                        <Checkbox m='5px' isChecked={item.split('!***!')[1]=== 'true'} colorScheme="blue" defaultIsChecked={item[1] === 'true'} style={{borderColor : '#3182ce', color:'#3182ce'}}
                        onChange={() => {
                            const auxCheck = [...dataFireStore.lists[dataFireStore.list]];
                            const aux = auxCheck[i].split('!***!')[0]; 
                            const auxLink = auxCheck[i].split('!***!')[2]?auxCheck[i].split('!***!')[2]:'';                        
                            auxCheck[i] = auxCheck[i].split('!***!')[1] === 'true'? `${aux}!***!false!***!${auxLink}`:`${aux}!***!true!***!${auxLink}`;

                            const data = dataFireStore;
                            data.lists[data.list] = auxCheck;
                            saveStateAndFirestore({ref, setState:setDataFireStore, newState:data});
                                                        
                        }}> 
                        {`${item.split('!***!')[0]}`}
                        
                        </Checkbox>
                        <a href={item.split('!***!')[2]? item.split('!***!')[2]:''}>{item.split('!***!')[2]?'(link)':null}</a>
                            
                        </HStack>
                      </Box>         
                      )}
                      </Draggable>);
                  }):null}
                  {provided.placeholder}
                </Box>
              )}
            </Droppable>
            </DragDropContext>
            </Box>


            <Container centerContent={true}>
            <HStack spacing={8}>
              
             
            </HStack>
            </Container>
        </>
    );
}

export default List;